.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.garage-label {
  text-align: center;
}

.window {
  width: 1100px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.garage {
  margin-bottom: 25px;
  position: relative;
  background: #F8F8F8;
  border: 3px solid #ECECEC;
}

.timeline {
  position: relative;
  width: 100%;
}

.title {
  margin: 0;
  padding: 0 30px 0 30px;
  font-weight: 500;
  font-size: 36px;
}


.data-picker {
  position: absolute;
  width: 100%;
  bottom: 34px;
  z-index: 3;
}

.fly {
  position: absolute;
}

.garages {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 59px;
}
